.main {
  display: flex; }

.wrapper {
  margin: 20px; }

.sidebar {
  background: black;
  padding: 0 50px;
  min-height: 100vh;
  padding-top: 50px;

  .menu {
    display: flex;
    flex-direction: column; }

  a {
    color: white;
    font-size: 16px;
    text-transform: uppercase;
    &:hover {
      color: red; } } }


.logout {
  position: absolute;
  right: 30px;
  top: 10px;
  font-weight: 600;
  font-size: 16px;
  transition: all .3s ease;

  &:hover {
    color: #666;
    cursor: pointer; } }

.activeLink {
  color: red!important; }
