.main {
  position: fixed;
  top: 100px;
  right: 0;
  background: #555;
  width: 280px;
  height: 100px;
  color: white;
  padding-left: 20px;
  padding-top: 10px;
  animation-duration: .7s;
  animation-name: slidein; }

.headerText {
  font-weight: 600;
  font-size: 20px;
  margin: 0;
  padding-bottom: 10px; }

.headerMain {
  padding: 0;
  margin: 0; }

@keyframes slidein {
  from {
    margin-right: -200px; }

  to {
    margin-right: 0; } }

.line {
  position: absolute;
  bottom: 10px;
  height: 5px;
  right: 0;
  width: 100%;
  background: white;
  animation-duration: 2s;
  animation-name: decreaseWidth; }

@keyframes decreaseWidth {
  from {
    width: 100%; }

  to {
    width: 0; } }
